@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
    font-family: 'Grand Hotel', ;
    src: url("https://fonts.googleapis.com/css?family=Grand+Hotel&display=swap&subset=latin-ext");
}

h1.custom {
    font-family: 'Pacifico', cursive;
    font-size: 6rem;
    margin: 0;
    text-align: center;
}

@media (max-width: 450px) {
    h1.custom {
        font-size: 4rem;
    }
}
.card.custom {
    width: 280px;
    -webkit-transition: all ease-in-out 200ms;
    transition: all ease-in-out 200ms;

}

.card.custom:hover {
    box-shadow: 0 .225rem .35rem rgba(0, 0, 0, .075);
    border-color: #c2c2c2;
    -webkit-transform: translateY(-4px);
            transform: translateY(-4px);
}

.card-img-top.custom {
    height: 250px;
    width: 100%;
}

.card-img-top.custom--comments {
    height: auto;
    max-height: 700px;
    width: 100%;
    object-fit: contain;
}

.list-group-item {
    border-left: none;
    border-right: none;
}

.card-img-top.custom img.img-fluid-custom {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.custom--comments__area {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}

.custom--comments__submit {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}

.list-group.custom--comments {
    max-height: calc(700px - (98px));
    overflow-y: auto;
}

.list-group-item:first-child {
    border-top: none;
}

.list-group-item:last-child {
    border-bottom: none
}

.btn.custom {
    font-size: 20px;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    -webkit-transition: all ease-in-out 200ms;
    transition: all ease-in-out 200ms;

    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.card.custom:hover .btn.btn-outline-danger {
    opacity: 0.5;
    visibility: visible;
}

.btn.btn-outline-danger {
    position: absolute;
    right: 0;
    bottom: 0;
    border-top-right-radius: 0;
    border-bottom-left-radius: 0;
    opacity: 0;
    visibility: hidden;
    -webkit-transition: all ease-in-out 200ms;
    transition: all ease-in-out 200ms;
    border-color: #ff4d5d;
    border-bottom: none;
    border-right: none;
}

.btn.btn-outline-danger:hover {
    background-color: #ff4d5d;
    border-color: #ff4d5d;
    opacity: 1 !important;
}

.btn.custom:hover {
    box-shadow: 0 0.3rem 1rem rgba(0, 0, 0, .375);
    border-radius: 10px;
    -webkit-animation: none;
            animation: none;
}

.btn.custom:active {
    background-color: #4e4e4e !important;
    box-shadow: none !important;
}

.form-custom {
    width: 400px;
}

@media (max-width: 600px) {
    .form-custom {
        max-width: 100%;
    }
}

.decoration-none {
    text-decoration: none !important;
    color: #1b1b1b;
}

.decoration-none:hover {
    color: #1b1b1b;
}

/*Inputs*/
.form-custom input.form-control {
    padding: 1.2rem 0.75rem;
}

.form-custom input.form-control::-webkit-input-placeholder {
    opacity: 0.6;
}

.form-custom input.form-control::-moz-placeholder {
    opacity: 0.6;
}

.form-custom input.form-control:-ms-input-placeholder {
    opacity: 0.6;
}

.form-custom input.form-control::-ms-input-placeholder {
    opacity: 0.6;
}

.form-custom input.form-control::placeholder {
    opacity: 0.6;
}

.form-custom input.form-control:focus {
    border-color: #868686;
    box-shadow: 0 0 0 0.2rem rgba(122, 122, 122, 0.09)
}

textarea {
    resize: none;
}

.return-button {
    height: 30px;
    width: 50px;
    text-decoration: none;
    position: relative;
    left: -15px;
}

@media (max-width: 450px) {
    .return-button {
        left: 50%;
        -webkit-transform: translate(-50%);
                transform: translate(-50%);
    }

    .list-group.custom--comments {
        max-height: none;
        overflow-y: visible;
    }
}

.return-button i {
    position: absolute;
    left: 10px;
    -webkit-animation: arrowMove infinite 1s alternate;
            animation: arrowMove infinite 1s alternate;
}

@-webkit-keyframes arrowMove {
    0% {
        left: 5px;
    }

    100% {
        left: 30px
    }
}

@keyframes arrowMove {
    0% {
        left: 5px;
    }

    100% {
        left: 30px
    }
}

.custom--card-content {
    text-decoration: none !important;
    position: absolute;
    right: -15px;
    top: -18px;
    color: inherit;
}

.card-text {
    word-break: break-word;
}

.card-text.--list {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.loader-list {
    position: absolute;
    top: -30px;
    z-index: -5;

    border: 16px solid #d8d8d8;
    border-top: 16px solid #636567;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    -webkit-animation: spin 1s linear infinite;
            animation: spin 1s linear infinite;
}

.loader-post {
    border: 16px solid #d8d8d8;
    border-top: 16px solid #636567;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    -webkit-animation: spin 1s linear infinite;
            animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
